// BASIC
import React, { Component } from 'react'
import styled, {keyframes} from 'styled-components'
import 'moment/locale/pl'
import {Link} from 'react-router-dom'
import latinize from 'latinize'
import {paramCase} from 'change-case'
import {db, au} from '../../../Config/firebase'
// COMPONENTS
import Loader from '../../Loader'
import Book from './components/Book'
import Header from './components/ListElement/Header'
import ListWrapper from './components/ListElement/ListWrapper'
import Work from '../Work'
// FUNCTIONS
import getIcon from '../../functions/getIcon'
// ICONS
import { FaHistory, FaAngleRight, FaStar, FaListUl, FaBomb, FaFire } from 'react-icons/fa'
import {GoKebabVertical} from 'react-icons/go'

const fade = keyframes`
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
`
const ListComponent = styled.div`
	width: 100vw;
	height: 100vh;
	height: calc(var(--vh, 1vh) * 100);
	display: flex;
	justify-content: center;
	align-items: center;
	animation: ${fade} 0.3s ease;
`
const Line = styled.hr`
	display: none;
	@media (min-width: 800px) and (min-height: 500px) {
		width: 0;
		height: calc(calc(var(--vh, 1vh) * 100) - 60px);
		max-height: 1000px;
		display: flex;
		margin: 0 30px;
		border: none;
		border-left: 3px solid var(--color-main);
		border-radius: 3px;
	}
`
const ListElementWrapper = styled.div``
const History = styled.div`
	background-color: var(--color-dark);
	padding: 20px 10px;
	border-radius: 20px;
	margin: 20px 0;
`
const HistoryInfo = styled.div`
	color: var(--color-secondary);
	text-align: center;
`
const LogIn = styled(Link)`
	font-weight: bold;
	color: var(--color-main);
	text-decoration: none;
	@media (hover: hover) {
		transition: all 0.1s ease;
		:hover {
			opacity: 0.7;
		}
	}
`
const HistoryTitle = styled.h2`
	font-size: 25px;
	text-align: center;
	margin: 0 0 20px 0;
	color: var(--color-main);
`
const UnitTitle = styled.h3`
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
	font-size: 20px;
`
const UnitList = styled.ul`
	list-style: none;
	@media (max-width: 400px) {
		padding-left: 20px;
	}
`
const Menu = styled(GoKebabVertical)`
	position: absolute;
	right: 0px;
	transform: translateY(3px);
	color: var(--color-main);
	/* @media (min-width: 600px) and (min-height: 500px) and (hover: hover) { */
	@media (hover: hover) {
		display: none;
	}
`
const listEntry = keyframes`
	0% {
		transform: translateX(-30px);
		opacity: 0;
	}
	100% {
		transform: translateX(0);
		opacity: 1;
	}
`
const listEntryMobile = keyframes`
	0% {
		transform: translateY(-7px) translateX(-30px);
		opacity: 0;
	}
	100% {
		transform: translateY(-7px) translateX(0);
		opacity: 1;
	}
`
const Links = styled.div`
	position: absolute;
	right: 0px;
	display: none;
	justify-content: flex-end;
	transform: translateX(-10px);
	transition: all 0.3s ease;
	animation: ${listEntryMobile} 0.5s 0.1s both;
	padding: 8px 0 5px 0;
	transform: translateY(-7px);
	margin-right: 20px;
	@media (max-width: 600px) {
		background-color: var(--color-dark);
		border-radius: 10px;
		/* animation: ${listEntryMobile} 0.5s 0.1s both; */
	}
`
const Go = styled(Link)`
	margin: 0 5px;
	color: var(--color-main);
	opacity: 1;
	transition: all 0.2s ease;
	:nth-child(1) {
		animation: ${listEntry} 0.3s 0.1s both;
	}
	:nth-child(2) {
		animation: ${listEntry} 0.4s 0.1s both;
	}
	:nth-child(3) {
		animation: ${listEntry} 0.5s 0.1s both;
	}
	:hover {
		color: rgb(255,223,0, 0.5);
	}
`
const UnitListItem = styled.li`
	position: relative;
	display: flex;
	justify-content: flex-start;
	padding: 3px;
	text-align: left;
	z-index: 3;
	@media (max-width: 600px) {
		padding-right: 30px;
	}
`
const Arrow = styled(FaAngleRight)`
	color: var(--color-main);
	min-width: 20px;
`
const Star = styled(FaStar)`
	color: var(--color-main);
	min-width: 20px;
	:first-child {
		margin-left: 20px;
	}
`
const PageLink = styled(Link)`
	display: block;
	text-align: left;
	text-decoration: none;
	color: var(--color-secondary);
	transition: all 0.15s ease-in;
	:hover {
		color: var(--color-primary);
	}
`


class List extends Component {
	state = {
		userId: null,
		recent: null,
		plannedDate: '20200128',
		units: null
	}
	componentDidMount() {
		let {bookName} = this.props.match.params;
		this.setState({bookName});
		au.onAuthStateChanged(user => {
			if (user) {
				this.setState({userId: user.uid})
				const recent = [];
				db.collection('users').doc(user.uid).get().then(snap => {
					if (snap.data()['easy-word'].recent[bookName]) {
						snap.data()['easy-word'].recent[bookName].slice(0, 3).forEach(({unit, part}) => {
							if (Number(unit.replace('unit_', '').replace('0', '')) <= 10) {
								db.collection('books').doc(bookName).get().then(snap => {
									recent.push({
										book: snap.data().info,
										unit: snap.data()[unit].title,
										part: part !== 'test' ? snap.data()[unit].parts[part].name : 'Test',
										unitUrl: part !== 'test' ? `rozdzial-${unit.replace('unit_', '').replace('0', '')}.${part.replace('part_', '').replace('0', '')}` : `rozdzial-${unit.replace('unit_', '').replace('0', '')}`
									})
									this.setState({recent})
								})
							}
							else {
								db.collection('books').doc(`${bookName}2`).get().then(snap => {
									if (snap.data()) {
										recent.push({
											book: snap.data().info,
											unit: snap.data()[unit].title,
											part: part !== 'test' ? snap.data()[unit].parts[part].name : 'Test',
											unitUrl: part !== 'test' ? `rozdzial-${unit.replace('unit_', '')}.${part.replace('part_', '').replace('0', '')}` : `rozdzial-${unit.replace('unit_', '')}`
										})
										this.setState({recent})
									}
								})
							}
						})
					}
					else {
						this.setState({recent})
					}
				})
			}
			db.collection('books').get().then((snaps) => {
				let units = [];
				let info = {}
				snaps.forEach(snap => {
					const data = snap.data()
					if (data.info.id.includes(bookName)) {
						Object.keys(data).sort().forEach((key) => {
							if (key !== 'info') {
								console.log(key)
								const parts = []
								Object.keys(data[key].parts).sort().forEach(part => {
									parts.push(data[key].parts[part])
								})
								units = units.concat({
									title: data[key].title,
									parts
								})
							}
						})
						if (data.info.id === bookName) {
							info = data.info
						}
					}
				})
				this.setState({units, info})
			})
		})
	}
	showLinks = (e) => {
		if (typeof(window.orientation) === 'undefined') {
			const children = e.currentTarget.children;
			children[children.length - 1].style.display = 'flex';
		}
	}
	hideLinks = (e) => {
		if (typeof(window.orientation) === 'undefined' || e === 'mobile') {
			document.querySelectorAll('.links').forEach(element => {
				if (element.style.display !== 'none') {
					element.style.display = 'none'
				}
			})
		}
	}
	showMobileLinks = (e) => {
		this.hideLinks('mobile')
		if (typeof(window.orientation) !== 'undefined') {
			const children = e.currentTarget.parentElement.children;
			children[children.length - 1].style.display = 'flex';
		}
	}
	render() {
		return (
			<ListComponent>
				{!this.state.units ? <Loader /> : <>
					{this.state.units.length !== 0 ? <>
						<Book src={this.state.info.src} information={this.state.info.alt}/>
						<Line />
						<ListElementWrapper>
							<Header />
							<ListWrapper>
								<History>
									<HistoryTitle><FaHistory /></HistoryTitle>
										{!this.state.recent ? <HistoryInfo>...</HistoryInfo> : <>
											{this.state.recent.length !== 0 ? this.state.recent.map(({book, unit, part, unitUrl}, index) => {
												return <div key={`history_${index + 1}`}>
													<UnitListItem onMouseOver={this.showLinks} onMouseOut={this.hideLinks}>
														<Arrow />
														<PageLink list="true" to={`/${this.state.bookName}/${unitUrl}/${paramCase(latinize(part))}`}>{part}<br />({unit})</PageLink>
														<Menu onClick={this.showMobileLinks} />
														<Links className="links">
															<Go to={`/${this.state.bookName}/spis-slowek/${unitUrl}/${paramCase(latinize(part))}`}>
																<FaListUl />
															</Go>
															<Go to={`/${this.state.bookName}/${unitUrl}/${paramCase(latinize(part))}/easy-shoot`}>
																<FaBomb />
															</Go>
															<Go to={`/${this.state.bookName}/${unitUrl}/${paramCase(latinize(part))}/easy-match`}>
																<FaFire />
															</Go>
														</Links>
													</UnitListItem>
												</div>
											}) : <>
												{!this.state.userId ? (
													<HistoryInfo><LogIn to="/login">Zaloguj si</LogIn> aby korzysta z historii wyszukiwania</HistoryInfo>
												) : (
													<HistoryInfo>Tutaj bd pojawia si ostatnio odwiedzone rozdziay</HistoryInfo>
												)}
											</>}
										</>}
								</History>
								{this.state.units.map(({title, parts}, unitIndex) => {
									return(
										<div key={`${unitIndex}_${title}`}>
											<UnitTitle>Unit {unitIndex + 1} - {title}</UnitTitle>
											<UnitList>
												{parts.length === 0 ? '' : parts.map(({name}, partIndex) => {
													return(
														<UnitListItem onMouseOver={this.showLinks} onMouseOut={this.hideLinks} key={`${partIndex}_${name}`}>
															<Arrow />
															<PageLink list="true" to={`/${this.state.bookName}/rozdzial-${unitIndex + 1}.${partIndex + 1}/${paramCase(latinize(name))}`}>{name}</PageLink>
															<Menu onClick={this.showMobileLinks} />
															<Links className="links">
																<Go to={`/${this.state.bookName}/spis-slowek/rozdzial-${unitIndex + 1}/${paramCase(latinize(name))}`}>
																	<FaListUl />
																</Go>
																<Go to={`/${this.state.bookName}/rozdzial-${unitIndex + 1}.${partIndex + 1}/${paramCase(latinize(name))}/easy-shoot`}>
																	<FaBomb />
																</Go>
																<Go to={`/${this.state.bookName}/rozdzial-${unitIndex + 1}.${partIndex + 1}/${paramCase(latinize(name))}/easy-match`}>
																	<FaFire />
																</Go>
															</Links>
														</UnitListItem>
													)
												})}
												<UnitListItem onMouseOver={this.showLinks} onMouseOut={this.hideLinks}>
													<Star />
													<PageLink list="true" to={`/${this.state.bookName}/rozdzial-${unitIndex + 1}/test`}>&nbsp;Test&nbsp;</PageLink>
													<Star />
													<Menu onClick={this.showMobileLinks} />
													<Links className="links">
														<Go to={`/${this.state.bookName}/spis-slowek/rozdzial-${unitIndex + 1}/test`}>
															<FaListUl />
														</Go>
														<Go to={`/${this.state.bookName}/rozdzial-${unitIndex + 1}/test/easy-shoot`}>
															<FaBomb />
														</Go>
														<Go to={`/${this.state.bookName}/rozdzial-${unitIndex + 1}/test/easy-match`}>
															<FaFire />
														</Go>
													</Links>
												</UnitListItem>
											</UnitList>
										</div>
									)
								})}
							</ListWrapper>
						</ListElementWrapper>
					</> : <Work />}
				</>}
			</ListComponent>
		);
	}
}

export default List