// BASIC
import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import styled from 'styled-components'
import {au, db} from '../../../Config/firebase'
// ICONS
import {MdChat, MdGroupAdd} from 'react-icons/md'
import {FaTimes} from 'react-icons/fa'

const MainChatComponent = styled.div`
	width: 100%;
	height: 100vh;
	padding: 30px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	text-align: center;
`
const Find = styled.div`
	width: 100%;
	display: flex;
	justify-content: center;
`
const Input = styled.input`
	width: 100%;
	max-width: 800px;
	background-color: var(--color-dark);
	border-radius: 30px;
	font-size: 20px;
	margin-bottom: 10px;
	padding: 8px 13px;
	border: none;
`
const Friends = styled.div`
	width: 100%;
	max-width: 1000px;
	height: calc(100vh - 130px);
	overflow-y: scroll;
	::-webkit-scrollbar {
		width: 0;
	}
`
const Friend = styled.div`
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin: 20px;
`
const Go = styled(Link)`
	max-width: calc(100% - 50px);
	display: flex;
	align-items: center;
	color: var(--color-primary);
	font-weight: bold;
	text-decoration: none;
	@media (hover: hover) {
		transition: all 0.2s ease;
		:hover {
			opacity: 0.7;
		}
	}
`
const Avatar = styled.div`
	min-width: 50px;
	height: 50px;
	background: url("${props => props.src}") no-repeat center center;
	background-size: cover;
	border-radius: 100%;
	margin-right: 10px;
`
const Nick = styled.div`
	width: 100%;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
`
const Contact = styled(Link)`
	display: block;
	font-size: 25px;
	color: var(--color-main);
	@media (hover: hover) {
		transition: all 0.2s ease;
		:hover {
			transform: scale(1.1);
		}
	}
`
const Add = styled.input`
	position: relative;
	border: none;
	width: 0;
	height: 0;
	::after {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: var(--color-primary);
	}
	:checked {
		background-color: var(--color-main);
	}
`
const CreateGroup = styled.div`
	width: 50px;
	height: 50px;
	position: fixed;
	bottom: 30px;
	right: 30px;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: var(--color-main);
	font-size: 30px;
	color: var(--color-bg);
	border-radius: 100%;
	@media (hover: hover) {
		transition: all 0.2s ease;
		:hover {
			cursor: pointer;
			transform: scale(1.1);
		}
	}
`

class MainChat extends Component {
	state = {
		userId: '',
		friends: []
	}
	componentDidMount() {
		au.onAuthStateChanged(user => {
			if (user) {
				const friends = []
				this.setState({userId: user.uid})
				db.collection('users').doc(user.uid).get().then(snap => {
					const chats = snap.data().chats
					this.setState({chats})
					Object.keys(chats).forEach(friendId => {
						console.log('chat: ', chats[friendId])
						db.collection('users').doc(friendId).get().then(snap => {
							const chatId = chats[friendId].chatId
							let userInfo = snap.data().info
							db.collection('chats').doc(chatId).get().then(snap => {
								userInfo.lastVisit = snap.data().lastVisit && snap.data().lastVisit[user.uid]
								friends.push(userInfo)
								this.setState({
									friends
								})
							})
						})
					})
				})

			}
		})
	}
	sortByLastVisit = (array) => {
		const compare = (a, b) => {
			a = a.lastVisit ? Number(a.lastVisit) : 0
			b = b.lastVisit ? Number(b.lastVisit) : 0
			if (a > b) {
				return -1
			}
			else if (a < b) {
				return 1
			}
			else {
				return 0
			}
		}
		return array.sort(compare)
	}
	createGroup = () => {
		// const name = window.prompt("Jak chcesz nazwa grup?")
		this.setState({createGroup: true})
	}
	back = () => {
		this.setState({createGroup: true})
	}
	render() {
		return (
			<MainChatComponent>
				<Find>
					<Input type="text" placeholder="Szukaj..." />
				</Find>
				<Friends>
					{this.state.friends.length === 0 ? '...' : this.sortByLastVisit(this.state.friends).map(({uid, nick, avatar}) => {
						// console.log(friend);
						return <Friend key={uid}>
							<Go to={`users/${uid}`}>
								<Avatar src={avatar} />
								<Nick>{nick}</Nick>
							</Go>
							{this.state.createGroup ? <Add type="checkbox" /> : <Contact to={`easy-chat/${this.state.chats[uid].chatId}`}>
								<MdChat />
							</Contact>}
						</Friend>
					})}
				</Friends>
				{!this.state.createGroup ? <CreateGroup onClick={this.createGroup}>
					<MdGroupAdd />
				</CreateGroup> : 
				<CreateGroup onClick={this.back}>
					<FaTimes />
				</CreateGroup>}
			</MainChatComponent>
		);
	}
}

export default MainChat