// BASIC
import React, { Component } from 'react'
import styled, {css} from 'styled-components'

const CardComponent = styled.div`
	width: 150px;
	height: 100px;
	perspective: 1000px;
	margin: 10px;
`
const CardElements = styled.div`
	position: relative;
	width: 100%;
	height: 100%;
	text-align: center;
	transition: transform 0.5s;
	transform-style: preserve-3d;
	box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
	${props =>
		props.show &&
		css`
			transform: rotateY(180deg);
		`
	};
`
const CardElement = styled.div`
	position: absolute;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	text-align: center;
	backface-visibility: hidden;
	border: 2px solid var(--color-decorative);
	border-radius: 20px;
	font-weight: bold;
	padding: 10px;
	${props =>
		props.front &&
		css`
			background-color: var(--color-decorative);
			color: var(--color-bg);
		`
	}
	${props =>
		props.back &&
		css`
			background-color: var(--color-bg);
			color: var(--color-decorative);
			transform: rotateY(180deg);
		`
	}
`
const Content = styled.span`
	font-size: 18px;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
`

class Card extends Component {
	state = {
		show: false
	}
	changeCardState = () => {
		this.setState({show: true})
		setTimeout(() => {
			this.setState({show: false})
		}, 700)
	}
	render() {
		return (
			<CardComponent onClick={this.changeCardState}>
				<CardElements show={this.state.show}>
					<CardElement front>
						{/* <Content>{this.props.word[`${this.props.languageOrder.word}1`]}</Content> */}
						{/* <Content>swko</Content> */}
					</CardElement>
					<CardElement back>
						<Content>{this.props.content}</Content> 
						{/* <Content>{this.props.word[`${this.props.languageOrder.translation}1`]}</Content>  */}
						{/* <Content>translation</Content>  */}
					</CardElement>
				</CardElements>
			</CardComponent>
		);
	}
}

export default Card