// BASIC
import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import styled, {keyframes, css} from 'styled-components'
import firebase, {db, au} from '../../../Config/firebase'
// COMPONENTS
import Loader from '../../Loader'
// IMAGES
import starsBg from '../../Images/stars-bg.svg'
// ICONS
import {TiPlus} from 'react-icons/ti'
import {FaStar, FaTimes} from 'react-icons/fa'
import {WiStars} from 'react-icons/wi'

const OpinionsPage = styled.div`
	width: 100vw;
	height: 100vh;
	height: calc(var(--vh, 1vh) * 100);
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 10px 30px;
	overflow-y: scroll;
	::-webkit-scrollbar {
		width: 0;
	}
`
const Header = styled.header`
	font-size: 30px;
	font-weight: bold;
	margin: 20px;
`
const fade = keyframes`
	from {
		opacity: 0;
		transform: scale(0.9);
	}
	to {
		opacity: 1;
		transform: scale(1);
	}
`
const Delete = styled(FaTimes)`
	position: absolute;
	top: 20px;
	right: 20px;
	opacity: 0.5;
	@media (hover: hover) {
		transition: all 0.1s ease;
		:hover {
			cursor: pointer;
			opacity: 1;
			transform: scale(1.1);
		}
	}
`
const Opinion = styled.div`
	position: relative;
	width: 100%;
	max-width: 1000px;
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 10px 0;
	padding: 20px 50px;
	background-color: var(--color-dark);
	border-radius: 20px;
	animation: ${fade} 0.3s ease;
	@media (min-width: 550px) {
		flex-direction: row;
	}
`
const User = styled.div`
	position: relative;
	margin-right: 0;
	@media (min-width: 550px) {
		margin-right: 30px;
	}
`
const Premium = styled(WiStars)`
	position: absolute;
	top: -15px;
	right: -15px;
	font-size: 40px;
	color: var(--color-decorative);
`
const Nick = styled.div`
	width: 100px;
	margin-top: 10px;
	text-align: center;
	font-weight: bold;
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
`
const Go = styled(Link)`
	display: block;
	width: 100px;
	height: 100px;
	border-radius: 100%;
	overflow: hidden;
`
const Avatar = styled.div`
	width: 100%;
	height: 100%;
	background: url("${props => props.src}") no-repeat center center;
	background-size: cover;
	@media (hover: hover) {
		transition: all 0.3s ease;
		:hover {
			opacity: 0.8;
			transform: scale(1.1);
		}
	}
`
const Content = styled.div`
	width: 100%;
	text-align: center;
	font-size: 16px;
	@media (min-width: 550px) {
		text-align: left;
		max-width: calc(100% - 130px);
	}
`
const Comment = styled.div`
	margin: 10px 0;
`
const Stars = styled.div`
	position: relative;
	width: 125px;
	height: 20px;
	margin: 10px auto;
	@media (min-width: 550px) {
		margin: 10px 0;
	}
	::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: var(--color-secondary);
	}
`
const StarsBg = styled.img`
	display: block;
	position: absolute;
	width: 125px;
	height: 20px;
`
const FillStars = styled.div`
	position: absolute;
	top: 0;
	left: 0;
	width: ${props => `${props.stars * 20}%`};
	height: 20px;
	background-color: var(--color-main);
`
const Info = styled.div`
	margin: 10px 0;
`
const LogIn = styled(Link)`
	font-weight: bold;
	color: var(--color-main);
	text-decoration: none;
	@media (hover: hover) {
		transition: all 0.1s ease;
		:hover {
			opacity: 0.5;
		}
	}
`
const AddComment = styled.div`
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 20px 0;
`
const Rating = styled.div`
	display: flex;
	direction: rtl;
`
const Star = styled(FaStar)`
	font-size: 40px;
	transition: all 0.1s ease;
	padding: 5px;
	color: var(--color-secondary);
	:hover {
		cursor: pointer;
		color: var(--color-main);
	}
	:hover ~ svg {
		color: var(--color-main);
	}
	${props =>
		props.checked &&
		css`
			color: var(--color-main);
		`
	};
`
const Input = styled.input`
	background-color: transparent;
	border: none;
	color: var(--color-primary);
	border-bottom: 1px solid var(--color-main);;
	padding: 5px;
	font-size: 16px;
	width: 100%;
	max-width: 500px;
	::placeholder {
		transition: all 0.3s ease;
	}
	:focus::placeholder {
		opacity: 0;
		transform: translateX(30px);
		letter-spacing: 5px;
	}
`
const Confirm = styled(TiPlus)`
	font-size: 25px;
	margin: 10px;
	color: var(--color-main);
	@media (hover: hover) {
		transition: all 0.1s ease;
		:hover {
			cursor: pointer;
			opacity: 0.5
		}
	}
`

class Opinions extends Component {
	state = {
		userId: null,
		isAdmin: false,
		opinions: null,
		views: null,
		users: null,
		userInfo: null
	}
	componentDidMount() {
		au.onAuthStateChanged(user => {
			if (user) {
				user.getIdTokenResult().then(idTokenResult => {
					if (idTokenResult.claims.admin) {
						this.setState({isAdmin: true})
					}
				})
				this.setState({userId: user.uid})
			}
			db.collection('apps').doc('easy-calc').onSnapshot(snap => {
				const entries = Object.entries(snap.data().stats.opinions)
				const sortedOpinions = entries.sort((a, b) => {
					if (a[1].stars < b[1].stars) {
						return 1;
					}
					else {
						return -1;
					}
				});
				this.setState({
					opinions: sortedOpinions,
					views: snap.data().stats.views,
				})
				db.collection('users').get().then(snaps => {
					let users = {}
					snaps.forEach(snap => {
						users[snap.id] = snap.data().info
						users[snap.id].premium = snap.data()['easy-word'].premium
					})
					let userInfo = {}
					sortedOpinions.forEach(opinion => {
						if (opinion[0] === (user ? user.uid : null)) {
							userInfo = opinion[1]
						}
					})
					this.setState({users, userInfo, stars: userInfo.stars})
				})
			})
		})
	}
	deleteComment = (uid) => {
		au.onAuthStateChanged(user => {
			if (user) {
				user.getIdTokenResult().then(idTokenResult => {
					if (idTokenResult.claims.admin || user.uid === uid) {
						db.collection('apps').doc('easy-calc').update({
							[`stats.opinions.${uid}`]: firebase.firestore.FieldValue.delete()
						})
					}
					else {
						alert('Nawet nie prbuj ')
					}
				})
			}
		})
	}
	addComment = (e) => {
		const comment = e.currentTarget.previousSibling.value;
		au.onAuthStateChanged(user => {
			if (user) {
				if (comment && this.state.stars) {
					db.collection('apps').doc('easy-calc').update({
						[`stats.opinions.${user.uid}`]: {
							comment: comment,
							stars: this.state.stars
						}
					})
				}
				else {
					if (!comment) {
						alert('Nic jeszcze nie wpisae!')
					}
					else {
						alert('Zaznacz ilo gwiazdek')
					}
				}
			}
		})
	}
	setStars = (e) => {
		this.setState({stars: Number(e.currentTarget.id.slice(-1))})
	}
	getInfo = (uid) => {
		let userInfo = null
		this.state.opinions.forEach(opinion => {
			if (opinion[0] === uid) {
				userInfo = opinion[1]
			}
		})
		return userInfo
	}
	render() {
		return (
			<OpinionsPage>
				<Header>Opinie</Header>
				{!this.state.users ? <Loader /> : Object.values(this.state.opinions).map((opinion) => {
					const {avatar, nick, premium} = this.state.users[opinion[0]];
					return (
						<Opinion key={`opinion_${opinion[0]}`}>
							{(this.state.isAdmin || this.state.userId === opinion[0]) && <Delete onClick={() => this.deleteComment(opinion[0])} />}
							<User>
								{premium && <Premium />}
								<Go to={`/users/${opinion[0]}`}>
									<Avatar src={avatar} />
								</Go>
								<Nick>{nick}</Nick>
							</User>
							<Content>
								<Stars>
									<FillStars stars={opinion[1].stars} />
									<StarsBg src={starsBg} alt='' />
								</Stars>
								<Comment>{opinion[1].comment}</Comment>
							</Content>
						</Opinion>
					)
				})}
				{this.state.users && <>
					{!this.state.userId ? <Info><LogIn to="/login">Zaloguj si</LogIn>, aby mc skomentowa aplikacj </Info> : <AddComment>
						<Rating onMouseOver={() => this.setState({rate: true})} onMouseLeave={() => this.setState({rate: false})}>
							<Star onClick={this.setStars} checked={this.state.stars >= 5 && !this.state.rate} id="star5" />
							<Star onClick={this.setStars} checked={this.state.stars >= 4 && !this.state.rate} id="star4" />
							<Star onClick={this.setStars} checked={this.state.stars >= 3 && !this.state.rate} id="star3" />
							<Star onClick={this.setStars} checked={this.state.stars >= 2 && !this.state.rate} id="star2" />
							<Star onClick={this.setStars} checked={this.state.stars >= 1 && !this.state.rate} id="star1" />
						</Rating>
						<Input placeholder="Napisz opini" defaultValue={this.state.userInfo.comment} />
						<Confirm onClick={this.addComment} />
					</AddComment>}
				</>}
			</OpinionsPage>
		);
	}
}

export default Opinions