// BASIC
import React, { Component } from 'react'
import styled, { css, keyframes } from 'styled-components'
// IMAGES
import LeftArrow from './Images/arrows/left-arrow.svg'
import RightArrow from './Images/arrows/right-arrow.svg'

const hide =  keyframes`
	0% {
		opacity: 1;
	}
	100% {
		opacity: 0;
	}
`
const PreloaderElement = styled.div`
	display: flex;
	justify-content: center;
	align-items: center;
	position: absolute;
	top: 0;
	width: 100%;
	height: 100%;
	background-color: black;
	z-index: 100;
	overflow: hidden;
	${props =>
		props.hide &&
		css`
			animation: ${hide} 0.3s both;
		`
	}
	${props =>
		props.delete &&
		css`
			display: none;
		`
	}
`

const Arrows = styled.div`
	position: relative;
	width: 100%;
	max-width: 512px;
	height: 100%;
	max-width: 512px;
	display: flex;
	justify-content: center;
	align-items: center;
`
const arrow =  keyframes`
	0% {
		opacity: 0;
		transform: translate(-90%, -50%);
	}
	70% {
		opacity: 1;
		transform: translate(-50%, -50%);
	}
	100% {
		opacity: 1;
		transform: translate(-50%, -50%);
	}
`
const Arrow = styled.img`
	width: 100%;
	position: absolute;
	left: 50%;
	top: 50%;
	transform: translate(-50%, -50%);
	:first-child {
		animation: ${arrow} 1s both;
	}
	:last-child {
		animation: ${arrow} 0.8s both;
	}
`

class Preloader extends Component {
	state = {
		hide: false,
		delete: false
	}
	componentDidMount() {
		setTimeout(() => {this.setState({hide: true})}, 1000);
		setTimeout(() => {this.setState({delete: true})}, 1300);
	}
	render() {
		return (
			<PreloaderElement hide={this.state.hide} delete={this.state.delete}>
				<Arrows>
					<Arrow src={LeftArrow} />
					<Arrow src={RightArrow} />
				</Arrows>
			</PreloaderElement>
		);
	}
}

export default Preloader