// BASIC
import React, { Component } from 'react'
import styled, {css, keyframes} from 'styled-components'
import firebase, {au, db, ms} from '../../../Config/firebase'
import {Link} from 'react-router-dom'
import moment from 'moment'
// ICONS
import {MdSend} from 'react-icons/md'
import {
	FaEdit, FaTimes, FaReply, FaTrash,
	FaHeart, FaLaughSquint, FaSurprise, FaSadTear, FaAngry, FaThumbsUp, FaThumbsDown
} from 'react-icons/fa'
import {} from 'react-icons/fa'
// IMAGES
import logo from '../../Images/icons/chat/logo.svg'
import love from '../../Images/icons/chat/love.svg'
import hi from '../../Images/icons/chat/hi.svg'
import fire from '../../Images/icons/chat/fire.svg'

moment.locale('pl')

const ChatSite = styled.div`
	width: 100vw;
	max-width: 1000px;
	height: 100vh;
	height: calc(var(--vh, 1vh) * 100);
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 0 auto;
	padding: 30px;
`
const fadeReactions = keyframes`
	from {
		opacity: 0;
		transform: scale(0.7);
	}
	to {
		opacity: 1;
		transform: scale(1);
	}
`
const Reactions = styled.div`
	position: absolute;
	top: ${props => `${props.top}px`};
	left: ${props => `${props.left}px`};
	height: 40px;
	width: 270px;
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	background-color: var(--chat-bg);
	border-radius: 30px 30px 30px 10px;
	transform-origin: bottom left;
	z-index: 2;
	transition: all 0.3s ease;
	animation: ${fadeReactions} 0.3s ease;
	${props =>
		props.owner === 'true' &&
		css`
			transform-origin: bottom right;
			border-radius: 30px 30px 10px 30px;
		`
	};
`
const fadeReaction = keyframes`
	from {
		opacity: 0;
		transform: scale(0.5) translateY(2px);
	}
	to {
		opacity: 1;
		transform: scale(1) translateY(2px);
	}
`
const Reaction = styled.div`
	padding: 5px;
	transform: translateY(2px);
	font-size: 23px;
	transition: 0.2s ease;
	:nth-child(1) {
		animation: ${fadeReaction} 0.2s ease;
	}
	:nth-child(2) {
		animation: ${fadeReaction} 0.3s ease;
	}
	:nth-child(3) {
		animation: ${fadeReaction} 0.4s ease;
	}
	:nth-child(4) {
		animation: ${fadeReaction} 0.5s ease;
	}
	:nth-child(5) {
		animation: ${fadeReaction} 0.6s ease;
	}
	:nth-child(6) {
		animation: ${fadeReaction} 0.7s ease;
	}
	:nth-child(7) {
		animation: ${fadeReaction} 0.8s ease;
	}
	${props =>
		props.checked &&
		css`
			color: var(--color-main);
		`
	};
`
const Friend = styled.div`
	width: 100%;
	height: 50px;
	display: flex;
	align-items: center;
	margin-bottom: 20px;
	color: var(--color-primary);
	text-decoration: none;
	transition: opacity 0.2s ease;
	:hover {
		opacity: 0.7;
	}
`
const Avatars = styled.div`
	display: flex;
`
const Avatar = styled.div`
	position: relative;
	width: 50px;
	height: 50px;
	background: url("${props => props.src}") no-repeat center center;
	background-size: cover;
	border-radius: 100%;
	margin-right: 10px;
	margin-left: -40px;
	:first-child {
		margin-left: 0;
	}
`
const Active = styled.div`
	position: absolute;
	top: -3px;
	right: -3px;
	width: 18px;
	height: 18px;
	background-color: green;
	border-radius: 100%;
	border: 2px solid var(--color-bg);
`
const Writing = styled.div`
	display: flex;
	margin-top: 15px;
`
const firstDott = keyframes`
	from {
		transform: translateY(0);
	}
	20% {
		transform: translateY(-5px);
	}
	40% {
		transform: translateY(0);
	}
	to {
		transform: translateY(0);
	}
`
const secondDott = keyframes`
	from {
		transform: translateY(0);
	}
	30% {
		transform: translateY(-5px);
	}
	60% {
		transform: translateY(0);
	}
	to {
		transform: translateY(0);
	}
`
const thirdDott = keyframes`
	from {
		transform: translateY(0);
	}
	40% {
		transform: translateY(-5px);
	}
	80% {
		transform: translateY(0);
	}
	to {
		transform: translateY(0);
	}
`
const Dott = styled.div`
	width: 5px;
	height: 5px;
	background-color: var(--color-main);
	margin: 0 3px;
	border-radius: 100%;
	:nth-child(1) {
		animation: ${firstDott} 1s ease infinite;
	}
	:nth-child(2) {
		animation: ${secondDott} 1s ease infinite;
	}
	:nth-child(3) {
		animation: ${thirdDott} 1s ease infinite;
	}
`
const Nick = styled.div`
	/* width: calc(100% - 110px); */
	font-size: 20px;
	font-weight: bold;
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;
`
const Messages = styled.div`
	position: relative;
	width: 100%;
	height: calc(100vh - 100px);
	height: calc(var(--vh, 1vh) * 100 - 170px);
	display: flex;
	flex-direction: column-reverse;
	align-items: flex-start;
	overflow-y: scroll;
	overflow-x: hidden;
	::-webkit-scrollbar {
		width: 0;
	}
`
const Time = styled.div`
	position: absolute;
	right: -50px;
	top: 50%;
	display: flex;
	align-items: center;
	transform: translateY(-50%);
	font-size: 15px;
	color: var(--color-secondary);
	opacity: 0;
	transition: opacity 0.2s ease;
	z-index: -1;
`
const Cancel = styled(FaTimes)`
	position: absolute;
	top: 50%;
	right: -30px;
	transform: translateY(-50%);
	color: var(--color-primary);
`
const MessageReactions = styled.div`
	position: absolute;
	bottom: -5px;
	height: 20px;
	display: flex;
	align-items: center;
	padding: 0 5px;
	background-color: var(--color-dark);
	border-radius: 20px;
	z-index: 2;
	${props =>
		props.owner !== 'true' &&
		css`
			right: -5px;
		`
	}
	${props =>
		props.owner === 'true' &&
		css`
			left: -5px;
		`
	}
`
const MessageReaction = styled.div`
	font-size: 13px;
	padding: 2px;
	color: var(--color-primary);
	svg {
		color: var(--color-main);
		/* opacity: 0.7; */
		transform: translateY(1px)
	}
	${props =>
		props.children > 1 &&
		css`
			:last-child {
				padding-left: 5px;
			}
		`
	};
	font-weight: bold;
`
const Option = styled.span`
	position: relative;
	font-size: 20px;
	color: var(--color-primary);
	margin: 0 5px;
	@media (hover: hover) {
		svg {
			transition: all 0.2s ease;
			:hover {
				color: var(--color-secondary);
			}
		}
	}
`
const fadeDate = keyframes`
	from {
		opacity: 0;
		transform: scale(0);
	}
	to {
		opacity: 1;
		transform: scale(1);
	}
`
const DateSent = styled.div`
	width: 120px;
	display: none;
	justify-content: center;
	font-size: 12px;
	text-align: center;
	animation: ${fadeDate} 0.3s ease;
	margin: 10px auto;
	line-height: 20px;
	transition: all 0.3s ease;
`
const BrowserLink = styled.a`
	color: var(--color-decorative);
	transition: opacity 0.2s ease;
	:hover {
		opacity: 0.5;
	}
`
const Reply = styled.div`
	width: auto;
	padding: 8px 13px;
	border-radius: 20px 20px 20px 5px;
	background-color: var(--chat-bg);
	opacity: 0.5;
	margin-bottom: 2px;
	${props =>
		props.owner === 'true' &&
		css`
			background-color: var(--chat-decorative);
			border-radius: 20px 20px 5px 20px;
		`
	};
`
const fade = keyframes`
	from {
		opacity: 0;
		transform: scale(1);
	}
	50% {
		opacity: 1;
		transform: scale(1.05);
	}
	to {
		opacity: 1;
		transform: scale(1);
	}
`
const Message = styled.div`
	position: relative;
	width: 100%;
	margin: 6px auto 6px 0;
	padding: 8px 13px;
	border-radius: 20px;
	background-color: var(--chat-decorative);
	${props =>
		props.replyId !== 'false' &&
		css`
			border-radius: 5px 20px 20px 20px;
			margin: 0 auto 6px 0;
			${props =>
				props.owner === 'true' &&
				css`
					border-radius: 20px 5px 20px 20px;
					margin: 0 0 6px auto;
				`
			};
		`
	};
`
const MessageWrapper = styled.div`
	position: relative;
	max-width: 80%;
	transition: all 0.3s ease;
	transform-origin: left;
	:first-child {
		margin-bottom: 20px;
		animation: ${fade} 0.3s ease;
	}
	@media (hover: hover) {
		:hover {
			cursor: pointer;
		}
	}
	${props =>
		props.owner === 'true' &&
		css`
			margin-left: auto;
			transform-origin: right;
			${Message} {
				background-color: var(--chat-bg);
			}
			${Time} {
				left: -50px;
			}
			${Cancel} {
				left: -30px;
			}
		`
	};
	${props =>
		props.removed === 'true' &&
		css`
			opacity: 0.5;
			:hover {
				cursor: default;
			}
		`
	};
`
const Sticker = styled.div`
	position: relative;
	width: 180px;
	min-height: 180px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	margin: 5px 0;
	background-color: var(--color-dark);
	border-radius: 30px;
	transition: all 0.2s ease;
	transform-origin: left;
	:first-child {
		animation: ${fade} 0.3s ease;
		margin-bottom: 20px;
	}
	@media (hover: hover) {
		:hover {
			cursor: pointer;
		}
	}
	${props =>
		props.owner === 'true' &&
		css`
			margin-left: auto;
			transform-origin: right;
			background-color: var(--color-dark);
			${DateSent} {
				text-align: right;
			}
			${Time} {
				left: -50px;
			}
		`
	};
`
const StickerImg = styled.img`
	width: 60%;
	height: 60%;
`
const Menu = styled.div`
	width: 100%;
	max-width: 1000px;
	position: fixed;
	bottom: 20px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0 20px;
`
const Send = styled.div`
	position: relative;
	min-width: 40px;
	height: 40px;
	display: flex;
	align-items: center;
	justify-content: center;
	color: var(--color-main);
	font-size: 30px;
	margin-left: 20px;
`
const StickerMenu = styled.div`
	position: absolute;
	bottom: 40px;
	left: 50%;
	display: flex;
	flex-direction: column;
	padding-bottom: 10px;
	transform: translateX(-50%);
`
const fadeSticker = keyframes`
	from {
		opacity: 0;
		transform: scale(0.7) translateY(50px);
	}
	to {
		opacity: 1;
		transform: scale(1) translateY(0);
	}
`
const SendSticker = styled.img`
	display: block;
	width: 40px;
	height: 40px;
	margin: 5px 0;
	:nth-child(1) {
		animation: ${fadeSticker} 0.5s ease;
	}
	:nth-child(2) {
		animation: ${fadeSticker} 0.4s ease;
	}
	:nth-child(3) {
		animation: ${fadeSticker} 0.3s ease;
	}
	@media (hover: hover) {
		transition: all 0.1s ease;
		:hover {
			cursor: pointer;
			transform: scale(1.1);
		}
	}
`
const Input = styled.input`
	width: calc(100% - 0px);
	border: none;
	background-color: var(--chat-bg);
	padding: 8px 13px;
	margin: 5px 0;
	border-radius: 30px;
	font-size: 18px;
	height: 40px;
	::placeholder {
		color: var(--color-primary);
		transition: all 0.2s ease;
	}
	:focus {
		::placeholder {
			opacity: 0;
			transform: translateX(20px);
			letter-spacing: 5px;
		}
	}
`
const Loader = styled.div`
	margin: 0 auto;
	font-size: 20px;
	font-weight: bold;
	color: var(--color-main);
	letter-spacing: 1px;
	${props =>
		props.hide === "true" &&
		css`
			opacity: 0;
		`
	};
`

class Chat extends Component {
	state = {
		friends: [],
		reactElement: '',
		isWriting: false,
		replyTo: {},
		pageX: 0,
		changeLimit: true,
		scroll: true,
		limit: 20,
		users: [],
		userId: '',
		content: [],
		message: '',
		lastId: 0,
		showStickers: false,
		showDate: false,
		showReactions: false,
		reactionsPosition: {
			x: 0,
			y: 0
		},
		wait: true,
		reactionsTypes: ['love', 'laugh', 'surprise', 'sad', 'angry', 'like', 'dislike']
	}
	componentDidMount() {
		this.mouseTimer = undefined
		au.onAuthStateChanged(user => {
			if (user) {
				db.collection('chats').doc(this.props.match.params.chatId).get().then(snap => {
					if(snap.data().users.includes(user.uid)) {
						db.collection('chats').doc(this.props.match.params.chatId).update({
							[`lastVisit.${user.uid}`]: new Date()
						})
						this.setState({
							userId: user.uid,
							users: snap.data().users
						})
						const friends = snap.data().users.filter((uid) => uid !== user.uid)
						friends.forEach(uid => {
							db.collection('users').doc(uid).get().then(snap => {
								this.setState(prevState => ({
									friends: prevState.friends.concat(snap.data().info)
								}));
							})
						})
						db.collection('status').doc('easy-word').onSnapshot(snap => {
							this.state.friends.forEach(({uid}) => {
								this.setState(prevState => ({
									friendsStatus: {
										...prevState.friendsStatus,
										[uid]: snap.data()[uid]
									}
								}));
							})
						})
						db.collection('chats').doc(this.props.match.params.chatId).collection('content').orderBy('createdAt', 'desc').limit(this.state.limit).onSnapshot(snaps => {
							const content = []
							snaps.forEach(snap => {
								content.push(snap.data())
							})
							this.setState({
								lastId: content.length ? Number(content[0].id) : 0,
								content: content,
								changeLimit: content.length < (content.length ? Number(content[0].id) : 0)
							})
						})
					}
					else {
						this.props.history.push('/')
					}
				})
			}
		})

	}
	sendMessage = (sticker) => {
		const newId = (this.state.lastId + 1).toString().padStart(10, '0')
		this.setState({scroll: true})
		if (this.state.message.trim()) {
			let data = {
				content: this.state.message.trim(),
				createdAt: new Date(),
				uid: this.state.userId,
				id: newId
			}
			if (this.state.replyTo.id) {
				data.replyTo = this.state.replyTo
			}
			this.setState({message: '', replyTo: {}})
			document.getElementById('message').value = '';
			document.getElementById('message').focus();
			db.collection('chats').doc(this.props.match.params.chatId).collection('content').doc(newId).set(data)
			db.collection('status').doc('easy-word').update({
				[this.state.userId]: 'online'
			})
		}
		else if (!this.state.message && this.state.showStickers) {
			this.setState({showStickers: false})
			const data = {
				sticker: sticker,
				createdAt: new Date(),
				uid: this.state.userId,
				id: newId
			}
			db.collection('chats').doc(this.props.match.params.chatId).collection('content').doc(newId).set(data)
		}
	}
	setMessage = (e) => {
		e.persist()
		this.setState({scroll: true, message: e.target.value})
		if (e.target.value.trim() && !this.state.isWriting) {
			this.setState({isWriting: true})
			db.collection('status').doc('easy-word').update({
				[this.state.userId]: 'writing...'
			})
		}
		else if (!e.target.value.trim() && this.state.isWriting) {
			this.setState({isWriting: false})
			db.collection('status').doc('easy-word').update({
				[this.state.userId]: 'online'
			})
		}
	}
	checkKey = (e) => {
		if (e.key === 'Enter') {
			this.sendMessage()
		}
	}
	increaseLimit = () => {
		this.setState(prevState => ({
			limit: prevState.limit + 20,
			scroll: false
		}), () => {
			db.collection('chats').doc(this.props.match.params.chatId).collection('content').orderBy('createdAt', 'desc').limit(this.state.limit).onSnapshot(snaps => {
				const content = []
				snaps.forEach(snap => {
					content.push(snap.data())
				})
				this.setState({
					content: content,
					changeLimit: content.length < this.state.lastId
				})
			})
		});
	}
	checkPosition = (e) => {
		e.persist()
		const bottom = Math.round(e.target.scrollTop) + 100 >= e.target.scrollHeight - e.target.clientHeight;
		if (bottom && this.state.scroll === false) {
			this.setState({scroll: true})
		}
		else if (!bottom && this.state.scroll === true) {
			this.setState({scroll: false})
		}
		if (e.target.scrollTop === 0 && this.state.changeLimit) {
			this.increaseLimit()
		}
	}
	showDate = (id) => {
		const element1 = document.getElementById(id).children[3];
		const element2 = document.getElementById(id).children[5];
		if (!element1.style.display || element1.style.display === 'none') {
			element1.style.display = 'flex'
			element2.style.display = 'flex'
		}
		else {
			this.hideDate(id)
		}
	}
	hideDate = (id) => {
		const element1 = document.getElementById(id).children[3];
		const element2 = document.getElementById(id).children[5];
		element1.style.opacity = '0'
		element1.style.transform = 'scale(0.5)'
		element2.style.opacity = '0'
		element2.style.transform = 'scale(0.5)'
		setTimeout(() =>{
			element1.style.display = 'none'
			element2.style.display = 'none'
			element1.style.opacity = '1'
			element1.style.transform = 'scale(1)'
			element2.style.opacity = '1'
			element2.style.transform = 'scale(1)'
		}, 300)
	}
	showTime = (id) => {
		const element = document.getElementById(id).children[0];
		if (element.style.opacity === '0' || !element.style.opacity) {
			element.style.opacity = '1'
		}
	}
	hideTime = (id) => {
		this.hideDate(id)
		const element = document.getElementById(id).children[0];
		if (element.style.opacity === '1') {
			element.style.opacity = '0'
		}
	}
	deleteMessage = (id) => {
		if (window.confirm('Na pewno chcesz usun wiadomo?')) {
			db.collection('chats').doc(this.props.match.params.chatId).collection('content').doc(id).update({
				removed: true
			})
		}
	}
	editMessage = (id, content) => {
		const newContent = prompt('Edytuj wiadomo:', content)
		if (newContent) {
			db.collection('chats').doc(this.props.match.params.chatId).collection('content').doc(id).update({
				edited: newContent
			})
		}
	}
	reactAs = (type) => {
		db.collection('chats').doc(this.props.match.params.chatId).collection('content').doc(this.state.reactElement).get().then(snap => {
			db.collection('chats').doc(this.props.match.params.chatId).collection('content').doc(this.state.reactElement).update({
				[`reactions.${this.state.userId}`]: (snap.data().reactions && snap.data().reactions[this.state.userId] === type) ? firebase.firestore.FieldValue.delete() : type
			})
		})
	}
	replyToMessage = (id) => {
		const element = document.getElementById(id)
		this.setState({replyTo: {
			id: id,
			uid: element.dataset.uid,
			content: element.children[4].innerText
		}})
		document.getElementById('message').focus();
	}
	startMove = (e) => {
		const element = e.currentTarget.parentElement;
		const childElement = e.currentTarget;
		if (this.mouseTimer) window.clearTimeout(this.mouseTimer)
		this.mouseTimer = window.setTimeout(() => {
			document.onselectstart = () => false;
			this.setState({showReactions: true})
			window.navigator.vibrate(30)
			document.getElementById("messages").style.overflowY = 'hidden';
			const owner = element.dataset.uid === this.state.userId
			const {left, right} = document.getElementById('messages').getBoundingClientRect()
			this.setState({
				reactElement: element.id,
				reactionsPosition: {
					y: element.getBoundingClientRect().y - 45,
					x: owner ? right - 270 : left,
					owner: owner,
					yourReaction: childElement.dataset.yourreaction
				}
			})
		}, 300)

		element.style.transition = 'opacity 0.2s ease, transform 0s ease'
		if (element.nextSibling.dataset.role === 'answer') {
			element.nextSibling.style.transition = 'opacity 0.2s ease, transform 0s ease'
		}
		this.setState({pageX: e.touches[0].pageX})
	}
	move = (e) => {
		const hoveredId = document.elementFromPoint(e.touches[0].clientX, e.touches[0].clientY).id
		if (this.state.reactionsTypes.includes(hoveredId)) {
			this.state.reactionsTypes.forEach(type => {
				document.getElementById(type).style.color = 'var(--color-primary)'
				document.getElementById(type).style.transform = 'translateY(2px)'
			})
			if (this.state.reactionsPosition.yourReaction === hoveredId) {
				document.getElementById(hoveredId).style.color = 'var(--color-light)'
			}
			else {
				document.getElementById(hoveredId).style.color = 'var(--color-main)'
			}
			document.getElementById(hoveredId).style.transform = 'scale(1.2)'
		}
		const element = e.currentTarget.parentElement;
		let subtractX = e.touches[0].pageX - this.state.pageX
		if (subtractX >= 50 || subtractX <= -50) {
			document.getElementById("messages").style.overflowY = 'hidden';
		}
		if (this.mouseTimer) window.clearTimeout(this.mouseTimer)
		if (!this.state.showReactions) {
				element.style.transform = `translateX(${(e.touches[0].pageX - this.state.pageX) / 2}px) scale(${(Math.abs(e.touches[0].pageX - this.state.pageX) / 1000) + 1})`
			if (element.nextSibling.dataset.role === 'answer') {
				element.nextSibling.style.transform = `translateX(${(e.touches[0].pageX - this.state.pageX) / 2}px)`
			}
			if ((subtractX >= 100 || subtractX <= -100) && element.dataset.checked !== 'yes') {
				element.dataset.checked = 'yes'
				window.navigator.vibrate(30)
			}
			else if ((subtractX < 100 && subtractX > -100) && !this.state.replyTo.id) {
				element.dataset.checked = 'no'
			}
		}
	}
	endMove = (e) => {
		if (this.mouseTimer) window.clearTimeout(this.mouseTimer)
		this.state.reactionsTypes.forEach(type => {
			if (document.getElementById(type) && document.getElementById(type).style.transform === 'scale(1.2)') {
				document.getElementById(type).click()
			}
		})
		this.hideReactions()
		document.onselectstart = () => true;
		document.getElementById("messages").style.overflowY = 'scroll';
		const element = e.currentTarget.parentElement;
		const childElement = e.currentTarget;
		element.style.transition = 'opacity 0.2s ease, transform 0.2s ease'
		element.style.transform = 'translateX(0)'
		if (element.nextSibling.dataset.role === 'answer') {
			element.nextSibling.style.transition = 'opacity 0.2s ease, transform 0.2s ease'
			element.nextSibling.style.transform = 'translateX(0)'
		}
		this.setState({pageX: 0})
		if (this.state.replyTo.id && this.state.replyTo.id !== element.id) {
			document.getElementById(this.state.replyTo.id).style.opacity = '1'
		}
		if (element.dataset.checked === 'yes') {
			this.setState({replyTo: {
				id: element.id,
				uid: element.dataset.uid,
				content: childElement.innerText
			}})
			document.getElementById('message').focus();
		}
	}
	cancel = () => {
		document.getElementById(this.state.replyTo.id).dataset.checked = 'no'
		this.setState({replyTo: {}})
	}
	showStickers = () => {
		if (!this.state.showStickers) {
			setTimeout(() => {
				this.setState({showStickers: true})
			}, 300)
		}
	}
	hideStickers = () => {
		if (this.state.showStickers) {
			this.setState({showStickers: false})
		}
	}
	hold = (e) => {
		if (!('ontouchstart' in window)) {
			const element = e.currentTarget.parentElement;
			const childElement = e.currentTarget;
			if (this.mouseTimer) window.clearTimeout(this.mouseTimer)
			document.onselectstart = () => false;
			this.mouseTimer = window.setTimeout(() => {
				this.setState({showReactions: true})
				window.navigator.vibrate(30)
				document.getElementById("messages").style.overflowY = 'hidden';
				const owner = element.dataset.uid === this.state.userId
				const {left, right} = document.getElementById('messages').getBoundingClientRect()
				this.setState({
					reactElement: element.id,
					reactionsPosition: {
						y: element.getBoundingClientRect().y - 45,
						x: owner ? right - 270 : left,
						owner: owner,
						yourReaction: childElement.dataset.yourreaction
					}
				})
			}, 300)
			document.onmousemove = (e) => {
				const hoveredId = document.elementFromPoint(e.clientX, e.clientY).id
				if (this.state.reactionsTypes.includes(hoveredId)) {
				this.state.reactionsTypes.forEach(type => {
					document.getElementById(type).style.color = 'var(--color-primary)'
					document.getElementById(type).style.transform = 'translateY(2px)'
				})
				if (this.state.reactionsPosition.yourReaction === hoveredId) {
					document.getElementById(hoveredId).style.color = 'var(--color-light)'
				}
				else {
					document.getElementById(hoveredId).style.color = 'var(--color-main)'
				}
				document.getElementById(hoveredId).style.transform = 'scale(1.2)'
				document.getElementById(hoveredId).style.cursor = 'pointer'
			}
			}
			document.onmouseup = () => {
				window.clearTimeout(this.mouseTimer)
				document.onmousemove = () => false;
				document.getElementById("messages").style.overflowY = 'scroll';
				this.state.reactionsTypes.forEach(type => {
					if (document.getElementById(type) && document.getElementById(type).style.transform === 'scale(1.2)') {
						document.getElementById(type).click()
					}
				})
				this.setState({
					reactionsPosition: {
						y: 0,
						x: 0
					}
				})
			}
		}
	}
	hideReactions = () => {
		if (document.getElementById('reactions')) {
			document.getElementById('reactions').style.opacity = '0'
			setTimeout(() => {
				this.setState({reactionsPosition: {x: 0, y: 0}})
			}, 300)
		}
	}
	scrollToMessage = (id) => {
		const prevId = (Number(id) + 1).toString().padStart(10, '0')
		if (this.state.lastId - Number(id) > this.state.content.length) {
			this.setState({
				limit: (this.state.lastId - Number(id)) + 20,
				scroll: false
			}, () => {
				db.collection('chats').doc(this.props.match.params.chatId).collection('content').orderBy('createdAt', 'desc').limit(this.state.limit).onSnapshot(snaps => {
					const content = []
					snaps.forEach(snap => {
						content.push(snap.data())
					})
					this.setState({
						content: content,
						changeLimit: content.length < this.state.lastId
					})
					if (document.getElementById(prevId)) {
						document.getElementById(prevId).scrollIntoView({ behavior: 'smooth',  block: 'end' });
						setTimeout(() => {
							document.getElementById(id).style.transform = 'scale(1.1)'
							setTimeout(() => {
								document.getElementById(id).style.transform = 'scale(1)'
							}, 300)
						}, 500)
					}
				})
			});
		}
		else {
			document.getElementById(prevId).scrollIntoView({ behavior: 'smooth',  block: 'end' });
			setTimeout(() => {
				document.getElementById(id).style.transform = 'scale(1.1)'
				setTimeout(() => {
					document.getElementById(id).style.transform = 'scale(1)'
				}, 300)
			}, 300)
		}
	}
	render() {
		return (
			<ChatSite>
				{/* <Loader><AiOutlineLoading /></Loader> */}
				{!this.state.userId ? '...' : <>
					{(this.state.reactionsPosition.x && this.state.reactionsPosition.y) ? (
						<Reactions
							id="reactions"
							top={this.state.reactionsPosition.y}
							left={this.state.reactionsPosition.x}
							owner={this.state.reactionsPosition.owner.toString()}
							onClick={this.hideReactions()}
						>
							<Reaction id="love" checked={this.state.reactionsPosition.yourReaction === 'love'} onClick={() => this.reactAs('love')}><FaHeart /></Reaction>
							<Reaction id="laugh" checked={this.state.reactionsPosition.yourReaction === 'laugh'} onClick={() => this.reactAs('laugh')}><FaLaughSquint /></Reaction>
							<Reaction id="surprise" checked={this.state.reactionsPosition.yourReaction === 'surprise'} onClick={() => this.reactAs('surprise')}><FaSurprise /></Reaction>
							<Reaction id="sad" checked={this.state.reactionsPosition.yourReaction === 'sad'} onClick={() => this.reactAs('sad')}><FaSadTear /></Reaction>
							<Reaction id="angry" checked={this.state.reactionsPosition.yourReaction === 'angry'} onClick={() => this.reactAs('angry')}><FaAngry /></Reaction>
							<Reaction id="like" checked={this.state.reactionsPosition.yourReaction === 'like'} onClick={() => this.reactAs('like')}><FaThumbsUp /></Reaction>
							<Reaction id="dislike" checked={this.state.reactionsPosition.yourReaction === 'dislike'} onClick={() => this.reactAs('dislike')}><FaThumbsDown /></Reaction>
						</Reactions>
					) : ''}
						<Friend>
							<Avatars>
								{this.state.friends.slice(0, 3).map((friend, index) => <Avatar key={index} src={friend.avatar}>
									{this.state.friendsStatus && Object.values(this.state.friendsStatus).includes(/online|writing.../) && <Active />}
								</Avatar>)}
							</Avatars>
							<Nick>{this.state.friends.map((friend, index) => this.state.friends.length !== index + 1 ? `${friend.nick}, ` : friend.nick)}</Nick>
							{/* {this.state.friendsStatus && this.state.friendsStatus[friend.uid] === 'writing...' && <Writing>
								<Dott />
								<Dott />
								<Dott />
							</Writing>} */}
						</Friend>
					<Messages id="messages" onScroll={this.checkPosition}>
						{this.state.content.map(({content, edited, createdAt, id, uid, type, removed, replyTo, sticker, reactions}) => {
							let date = new Date(1970, 0, 1)
							let reactionsLength = 0
							const allReactions = reactions
							if (reactions) {
								const reactionsOrder = {
									love: 0.7,
									laugh: 0.6,
									surprise: 0.5,
									sad: 0.4,
									angry: 0.3,
									like: 0.2,
									dislike: 0.1
								}
								function compare(a, b) {
									if (reactionsOrder[a] > reactionsOrder[b])
										return -1
									if (reactionsOrder[a] < reactionsOrder[b])
										return 1
									return 0
								}
								Object.values(reactions).forEach(reaction => {
									reactionsOrder[reaction] += 1
									reactionsLength += 1
								})
								reactions = Object.values(reactions).sort(compare)
								reactions = [...new Set(reactions)]
							}
							if (edited) {
								content = edited
							}
							date.setSeconds(createdAt.seconds)
							if (!type && !removed && content) {
								return <React.Fragment key={id}>
									<MessageWrapper
										onMouseOver={() => this.showTime(id)}
										onMouseLeave={() => this.hideTime(id)}
										owner={(this.state.userId === uid).toString()}
										id={id}
										data-uid={uid}
									>
										<Time>
											{moment(date).add(2, 'hours').format('LT')}
										</Time>
										<div>{this.state.replyTo.id === id && <Cancel onClick={this.cancel} />}</div>
										<div>{(reactions && reactions.length > 0) && (
										<MessageReactions owner={(this.state.userId === uid).toString()}>
											{reactions.slice(0, 2).map(reaction => {
												if (reaction === 'love') {
													return <MessageReaction><FaHeart /></MessageReaction>
												}
												else if (reaction === 'laugh') {
													return <MessageReaction><FaLaughSquint /></MessageReaction>
												}
												else if (reaction === 'surprise') {
													return <MessageReaction><FaSurprise /></MessageReaction>
												}
												else if (reaction === 'sad') {
													return <MessageReaction><FaSadTear /></MessageReaction>
												}
												else if (reaction === 'angry') {
													return <MessageReaction><FaAngry /></MessageReaction>
												}
												else if (reaction === 'like') {
													return <MessageReaction><FaThumbsUp /></MessageReaction>
												}
												else if (reaction === 'dislike') {
													return <MessageReaction><FaThumbsDown /></MessageReaction>
												}
												else return ''
											})}
											{reactionsLength > 1 && <MessageReaction>{reactionsLength}</MessageReaction>}
										</MessageReactions>
										)}</div>
										<DateSent>
											{moment(date).add(2, 'hours').format('LL').replace(/[0-9]{4}/, '')}
										</DateSent>
										<Message
											onMouseDown={this.hold}
											onTouchStart={this.startMove}
											onTouchMove={this.move}
											onTouchEnd={this.endMove}
											onClick={() => this.showDate(id)}
											replyId={replyTo ? replyTo.id : 'false'}
											owner={(this.state.userId === uid).toString()}
											data-yourreaction={allReactions ? allReactions[this.state.userId] : ''}
										>
											{content.includes('http') ? (
												content.replace(/http.+/, x => `===${x}===`).split('===').map(fragment => {
													if (fragment.includes('http')) {
														return <BrowserLink target="_blank" href={fragment} rel="noopener noreferrer">{fragment}</BrowserLink>
													}
													else {
														return fragment
													}
												})
												) : (
													content)}
										</Message>
										<DateSent>
											<Option onClick={() => this.replyToMessage(id)}>
												<FaReply />
											</Option>
											{this.state.userId === uid && <>
												<Option onClick={() => this.deleteMessage(id)}>
													<FaTrash />
												</Option>
												<Option onClick={() => this.editMessage(id, content)}>
													<FaEdit />
												</Option>
											</>}
										</DateSent>
									</MessageWrapper>
									<MessageWrapper
										data-role="answer"
										owner={(this.state.userId === uid).toString()}
										onClick={() => replyTo && this.scrollToMessage(replyTo.id)}
									>
										{replyTo && <Reply owner={(this.state.userId === uid).toString()}>{replyTo.content}</Reply>}
									</MessageWrapper>
								</React.Fragment>
							}
							else if ((sticker || type === 'logo') && !removed) {
								return <React.Fragment key={id}>
									<Sticker
										onClick={() => this.showDate(id)}
										onMouseOver={() => this.showTime(id)}
										onMouseLeave={() => this.hideTime(id)}
										owner={(this.state.userId === uid).toString()}
										id={id}
									>
										<Time>
											{moment(date).add(2, 'hours').format('LT')}
										</Time>
										<div>{this.state.replyTo.id === id && <Cancel onClick={this.cancel} />}</div>
										<div>{reactions && (
										<MessageReactions>
											{reactions.includes('love') && <MessageReaction><FaHeart /></MessageReaction>}
											{reactions.includes('laugh') && <MessageReaction><FaLaughSquint /></MessageReaction>}
											{reactions.includes('surprise') && <MessageReaction><FaSurprise /></MessageReaction>}
											{reactions.includes('sad') && <MessageReaction><FaSadTear /></MessageReaction>}
											{reactions.includes('angry') && <MessageReaction><FaAngry /></MessageReaction>}
											{reactions.includes('like') && <MessageReaction><FaThumbsUp /></MessageReaction>}
											{reactions.includes('dislike') && <MessageReaction><FaThumbsDown /></MessageReaction>}
										</MessageReactions>
										)}</div>
										<DateSent>{moment(date).add(2, 'hours').format('LL').replace(/[0-9]{4}/, '')}</DateSent>
										{/* <div>{replyTo && <Reply owner={(this.state.userId === uid).toString()}>{replyTo.content}</Reply>}</div> */}
										{type === 'logo' ? <StickerImg src={logo} /> : <StickerImg src={require(`../../Images/icons/chat/${sticker}.svg`)} />}
										<DateSent>
											{this.state.userId === uid && <>
												<Option onClick={() => this.deleteMessage(id)}>
													<FaTrash />
												</Option>
											</>}
										</DateSent>
									</Sticker>
									<MessageWrapper
										data-role="answer"
										owner={(this.state.userId === uid).toString()}
									>
										{replyTo && <Reply owner={(this.state.userId === uid).toString()}>{replyTo.content}</Reply>}
									</MessageWrapper>
								</React.Fragment>
							}
							else if (removed) {
								return <MessageWrapper
									owner={(this.state.userId === uid).toString()}
									key={id}
									id={id}
									removed="true"
								>
									<Message replyId="false">Wiadomo usunita</Message>
								</MessageWrapper>
							}
						})}
						<Loader hide={(!this.state.changeLimit).toString()}>...</Loader>
					</Messages>
					<Menu>

						<Input onFocus={this.hideStickers} placeholder={!this.state.replyTo.id ? 'Wpisz wiadomo...' : 'Odpowiedz...'} autoComplete="off" id="message" onChange={this.setMessage} onKeyPress={this.checkKey} />
						{this.state.message ? (<Send onClick={this.sendMessage}>
							<MdSend />
						</Send>) : (<Send onMouseLeave={this.hideStickers}>
							{this.state.showStickers && <StickerMenu >
								<SendSticker onClick={() => this.sendMessage('love')} src={love} />
								<SendSticker onClick={() => this.sendMessage('fire')} src={fire} />
								<SendSticker onClick={() => this.sendMessage('hi')} src={hi} />
							</StickerMenu>}
							<SendSticker onMouseOver={this.showStickers} onClick={() => this.sendMessage('logo')} src={logo} />
						</Send>)}
					</Menu>
				</>}
			</ChatSite>
		);
	}
}

export default Chat