// BASIC
import React, { Component } from 'react'
import styled from 'styled-components'
// COMPONENTS
import Card from './Card'

const CardsComponent = styled.div`
	width: 100%;
	height: calc(100% - 100px);
	max-width: 1000px;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
	overflow-y: scroll;
	margin: 20px;
`

class Cards extends Component {
	render() {
		console.log(this.props.words);
		return (
			<CardsComponent>
				{!this.props.words ? '...' : this.props.words.map(word => <Card content={word[`${this.props.languageOrder.translation}1`]} />)}
				{/* {!this.props.words ? '...' : this.props.words.map(word => <Card content={word[`${this.props.languageOrder.translation}1`]} />)} */}
			</CardsComponent>
		);
	}
}

export default Cards