// BASIC
import React, { Component } from 'react'
import styled, {keyframes, css} from 'styled-components'
import {db, au} from '../../../Config/firebase'
// FUNCTIONS
import getWord from '../functions/chooseWord'
// IMAGES
import good from '../../Images/good.png'
import pl from '../../Images/icons/pl.svg'
import uk from '../../Images/icons/uk.svg'
// ICONS
import {FaHeart, FaTimes, FaSyncAlt} from 'react-icons/fa'

const EasyMatchSite = styled.div`
	position: relative;
	width: 100vw;
	height: 100vh;
	height: calc(var(--vh, 1vh) * 100);
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 30px;
	font-size: 20px;
	overflow: hidden;
`
const Stats = styled.div`
	width: 100%;
	display: flex;
	justify-content: space-between;
`
const StatsElement = styled.div`
`
const StatsNumber = styled.strong`
	color: var(--color-main);
`
const base = keyframes`
	from {
		opacity: 0;
		transform: translate(-50%, -50%) scale(0.9);
	}
	to {
		opacity: 1;
		transform: translate(-50%, -50%) scale(1);
	}
`
const Card = styled.div`
	width: 90vw;
	max-width: 600px;
	height: calc(100vh - 200px);
	max-height: 700px;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	animation: ${base} 0.2s ease;
`
const CardContent = styled.div`
	width: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	padding: 20px;
	background-color: var(--color-dark);
	border-radius: 30px;
	transition: opacity 0.2s ease, left 0s ease, transform 0s ease;
	transform-origin: bottom;
	:hover {
		cursor: pointer;
	}
`
const CardPart = styled.div`
	position: absolute;
	top: 0;
	height: 100%;
	width: 50%;
	z-index: 2;
	${props =>
		props.nope &&
		css`
			left: 0;
			/* background-color: red; */
		`
	}
	${props =>
		props.like &&
		css`
			right: 0;
			/* background-color: green; */
		`
	}
`
const SwipeImage = styled.img`
	position: absolute;
	opacity: 0;
	top: -20px;
	${props =>
		props.nope &&
		css`
			right: -40px;
			transform: rotate(10deg);
		`
	}
	${props =>
		props.like &&
		css`
			left: -40px;
			transform: rotate(-10deg);
		`
	}
`
const EndImage = styled.img`
	width: 70%;
	margin-bottom: 50px;
`
const showTitle = keyframes`
	from {
		opacity: 0;
		transform: scale(0.9);
	}
	to {
		opacity: 1;
		transform: scale(1);
	}
`
const Title = styled.h2`
	width: 100%;
	font-size: 30px;
	min-height: 25px;
	margin-bottom: 30px;
	padding: 0 20px;
	text-align: center;
	animation: ${showTitle} 0.3s ease;
	${props =>
		props.dotted &&
		css`
			font-size: 25px;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
		`
	};
	${props =>
		props.hide &&
		css`
			display: none;
		`
	};
`
const Image = styled.div`
	position: relative;
	width: 100%;
	height: 70%;
	background: url("${props => props.src}") no-repeat center top;
	background-size: cover;
	border-radius: 20px;
	margin-bottom: 50px;
`
const fadeAnswer = keyframes`
	from {
		opacity: 0;
	}
	to {
		opacity: 0.5;
	}
`
const answerAnimation = keyframes`
	from {
		opacity: 0;
		transform: scale(0.8);
	}
	50% {
		opacity: 1;
		transform: scale(1.1);
	}
	to {
		opacity: 1;
		transform: scale(1);
	}
`
const Answer = styled.div`
	width: 100%;
	height: 100%;
	display: none;
	justify-content: center;
	align-items: center;
	text-align: center;
	position: absolute;
	top: 0;
	left: 0;
	padding: 0 30px;
	background-color: var(--color-dark);
	opacity: 0.5;
	font-size: 30px;
	font-weight: bold;
	color: var(--color-light);
	border-radius: 20px;
	@media (min-width: 700px) and (min-height: 700px) {
		font-size: 50px;
	}
	animation: ${fadeAnswer} 0.2s both;
	::after {
		content: "${props => props.content}";
		display: -webkit-box;
		-webkit-line-clamp: 3;
		-webkit-box-orient: vertical;
		overflow: hidden;
		animation: ${answerAnimation} 0.3s both;
	}
	${props =>
		props.show &&
		css`
			display: flex;
		`
	};
`
const Decision = styled.div`
	position: absolute;
	bottom: 30px;
	display: flex;
`
const Icon = styled.div`
	font-size: 35px;
	color: var(--color-main);
	margin: 0 10px;
	transition: all 0.1s ease;
	transform: translateY(5px);
	:hover {
		cursor: pointer;
	}
	:active {
		transform: scale(1.1);
	}
`
const Language = styled.img`
	width: 45px;
	margin: 0 10px;
	transition: all 0.1s ease;
	:hover {
		cursor: pointer;
	}
	:active {
		transform: scale(0.9);
	}
`

class TinderApp extends Component {
	state = {
		languageOrder: {
			word: 'word',
			translation: 'translation'
		},
		pageX: 0,
		swipe: '',
		words: [],
		baseWord: {},
		goodAnswers: [],
		badAnswers: [],
		uncertainAnswers: [],
		lastAnswer: true,
		showAnswer: false,
		stop: false,
		animation: '',
		pause: false
	}
	componentDidMount() {
		this.newGame();
		document.addEventListener('keydown', (e) => {
			e = e || window.event
			if (this.state.baseWord) {
				if (e.keyCode === 39) {
					this.like()
				}
				else if (e.keyCode === 37) {
					this.nope()
				}
			}
		})
		au.onAuthStateChanged(user => {
			if (user) {
				// db.collection('users').doc(user.uid).get().then(snap => {
				// 	db.collection('users').doc(user.uid).update({
				// 		[`easy-word.recent.${this.props.match.params.bookName}`]: [
				// 			{
				// 				unit,
				// 				part
				// 			},
				// 			...(snap.data()['easy-word'].recent[this.props.match.params.bookName] ? snap.data()['easy-word'].recent[this.props.match.params.bookName].filter((history) => history.unit !== unit || history.part !== part) : [])
				// 		]
				// 	})
				// })
			}
		})
		if (document.getElementById('card')) {
			document.getElementById('card').addEventListener('touchstart', (e) => {
				if (this.state.pageX === 0) {
					document.getElementById('card').style.translation = `none`;
					console.log(e.touches[0].pageX, e.type)
					this.setState({pageX: e.touches[0].pageX, pause: true})
				}
			})
			document.getElementById('card').addEventListener('touchmove', (e) => {
				if (!this.state.stop) {
					let subtract = this.state.pageX - e.touches[0].pageX
					document.getElementById('card').style.left = `calc(50% - ${subtract}px)`;
					document.getElementById('card').style.transform = `translate(-50%, -50%) rotate(${-subtract / 20}deg)`;
					// if (subtract > 0) {
					// 	document.getElementById('nope').style.opacity = subtract / 50
					// }
					// else if (subtract < 0) {
					// 	document.getElementById('like').style.opacity = -subtract / 50
					// }
					if (subtract > 50 && this.state.swipe !== 'nope') {
						this.setState({swipe: 'nope'})
						// console.log('nope');
					}
					else if ((subtract < 50 && subtract > -50) && this.state.swipe !== 'no') {
						this.setState({swipe: 'no'})
						// console.log('no');
					}
					else if (subtract < -50 && this.state.swipe !== 'like') {
						this.setState({swipe: 'like'})
						// console.log('like');
					}
				}
			})
			document.getElementById('card').addEventListener('touchend', (e) => {
				if (this.state.pageX !== 0) {
					console.log(e.type)
					if (this.state.swipe === 'nope') {
						this.nope()
					}
					else if (this.state.swipe === 'like') {
						this.like()
					}
					else if (this.state.swipe === 'no') {
						document.getElementById('card').style.transition = `left 0.2s ease, opacity 0.2s ease, transform 0s ease`;
						document.getElementById('card').style.transform = `translate(-50%, -50%) rotate(0)`;
						document.getElementById('card').style.left = `50%`;
						setTimeout(() => {
							document.getElementById('card').style.transition = `left 0s ease, opacity 0.2s ease, transform 0s ease`;
						}, 200)
					}
					// document.getElementById('like').style.opacity = 0
					// document.getElementById('nope').style.opacity = 0
					this.setState({pageX: 0, swipe: '', pause: false})
				}
			})
		}
	}
	newGame = () => {
		let {bookName, unitNumber} = this.props.match.params;
		if (bookName === 'repetytorium') {
			bookName = 'book_01'
		}
		else if (bookName === 'wsip') {
			bookName = 'book_02'
		}
		else if (bookName === 'jezyk-angielski-zawodowy') {
			bookName = 'book_03'
		}
		else if (bookName === 'znaki-drogowe') {
			bookName = 'book_04'
		}
		else if (bookName === 'czasowniki-nieregularne') {
			bookName = 'book_05'
		}
		let partNumber;
		if (unitNumber.includes('.') && Number(unitNumber)) {
			partNumber = unitNumber.replace(/[0-9]*\./g,'');
			unitNumber = unitNumber.replace(/\.[0-9]*/g, '').padStart(2, '0');
		}
		else if (Number(unitNumber)) {
			unitNumber = unitNumber.padStart(2, '0');
		}
		else {
			alert('nie ma strony!');
		}
		const unit = `unit_${unitNumber}`;
		const part = partNumber ? `part_${partNumber.padStart(2, '0')}` : 'test';
		db.collection('books').get().then((snaps) => {
			let units = [];
			let result = {};
			snaps.forEach(snap => {
				if (snap.data().info.id.includes(this.props.match.params.bookName)) {
					units = units.concat(Object.values(snap.data()).slice(1))
				}
			})
			units.forEach((prop, index) => result[`unit_${(index + 1).toString().padStart(2, '0')}`] = prop);
			let partWords = [];
			const words = result[unit];
			if (part !== 'test') {
				partWords = Object.values(words.parts[part].words);
			}
			else {
				Object.values(words.parts).forEach(({words}) => {
					words = words ? Object.values(words) : [];
					partWords = partWords.concat(Object.values(words));
				});
			}
			this.setState({
				goodAnswers: [],
				words: partWords,
				baseWord: getWord(partWords),
				info: {
					unit,
					part,
					bookName
				},
			});
		})
	}
	changeLanguage = () => {
		if (this.state.languageOrder.word === 'word') {
			this.setState({
				languageOrder: {
					word: 'translation',
					translation: 'word'
				},
			})
		}
		else {
			this.setState({
				languageOrder: {
					word: 'word',
					translation: 'translation'
				},
			})
		}
	}
	getAnswer = () => {
		if (!this.state.stop) {
			if (!this.state.showAnswer) {
				this.setState({showAnswer: true})
				setTimeout(() => {
					this.setState({showAnswer: false})
				}, 800);
			}
			else {
				this.setState({showAnswer: false})
			}
		}
	}
	getNew = () => {
		this.setState({
			animation: '',
			showAnswer: false
		})
		if (this.state.badAnswers.length > 0 && this.state.lastAnswer) {
			this.setState({
				baseWord: this.state.badAnswers[0]
			})
		}
		else {
			this.setState({
				baseWord: getWord(this.state.words)
			})
		}
	}
	like = () => {
		if (!this.state.animation) {
			document.getElementById('card').style.transition = `left 0.2s ease, opacity 0.2s ease, transform 0.2s ease`;
			document.getElementById('card').style.transform = 'translate(0%, -50%) rotate(5deg)';
			document.getElementById('card').style.opacity = '0';
			this.setState({animation: true, lastAnswer: true})
			if (!this.state.goodAnswers.includes(this.state.baseWord)) {
				this.state.goodAnswers.push(this.state.baseWord)
			}
			const goodWords = [];
			this.state.goodAnswers.forEach(answer => goodWords.push(answer.word1))
			this.setState({
				words: this.state.words.filter(({word1}) => !goodWords.includes(word1))
			})
			setTimeout(() => {
				if (this.state.badAnswers.length > 0) {
					this.state.badAnswers.shift()
				}
				this.getNew()
				document.getElementById('card').style.transform = 'translate(-50%, -50%) rotate(0)';
				document.getElementById('card').style.opacity = '1';
				document.getElementById('card').style.left = `50%`;
				setTimeout(() => {
					document.getElementById('card').style.transition = `left 0s ease, opacity 0s ease, transform 0s ease`;
				}, 200)
			}, 200)
		}
	}
	nope = () => {
		const swipeNope = () => {
			document.getElementById('card').style.transform = 'translate(-100%, -50%) rotate(-5deg)';
			document.getElementById('card').style.opacity = '0';
			this.setState({animation: true, lastAnswer: this.state.badAnswers.length >= 3 ? true : false})
			const badWords = [];
			this.state.badAnswers.forEach(answer => badWords.push(answer.word1))
			this.setState({
				words: this.state.words.filter(({word1}) => !badWords.includes(word1))
			})
			if (!badWords.includes(this.state.baseWord.word1)) {
				this.state.badAnswers.push(this.state.baseWord)
			}
			setTimeout(() => {
				this.getNew()
				document.getElementById('card').style.transform = 'translate(-50%, -50%) rotate(0)';
				document.getElementById('card').style.opacity = '1';
				document.getElementById('card').style.left = `50%`;
			}, 200)
		}
		if (!this.state.animation && !this.state.stop) {
			if (!this.state.showAnswer) {
				this.setState({showAnswer: true, stop: true})
				document.getElementById('card').style.transition = `left 0.2s ease, opacity 0.2s ease, transform 0.2s ease`;
				document.getElementById('card').style.transform = `translate(-50%, -50%) rotate(0)`;
				document.getElementById('card').style.left = `50%`;
				setTimeout(() => {
					setTimeout(() => {
						this.state.showAnswer && this.setState({showAnswer: false, stop: false})
						setTimeout(() => {
							document.getElementById('card').style.transition = `left 0s ease, opacity 0s ease, transform 0s ease`;
						}, 200)
					}, 200)
					swipeNope()
				}, 800)
			}
			else {
				swipeNope()
			}
		}
	}
	render() {
		const {image} = this.state.baseWord ? this.state.baseWord : {};
		const word = this.state.baseWord ? this.state.baseWord[`${this.state.languageOrder.word}1`] : ''
		const translation = this.state.baseWord ? this.state.baseWord[`${this.state.languageOrder.translation}1`] : ''
		return (
			<EasyMatchSite>
				<Stats>
					<StatsElement>Umiem: <StatsNumber>{this.state.goodAnswers.length}</StatsNumber></StatsElement>
					<StatsElement>Nie umiem: <StatsNumber>{this.state.badAnswers.length}</StatsNumber></StatsElement>
				</Stats>
				{/* <div id="test">test</div> */}
					<Card id="card">
						{!this.state.baseWord ? (
							<CardContent>
								<Title>Koniec gry! wietna robota!</Title>
								<EndImage src={good} />
								<Icon onClick={this.newGame}>
									<FaSyncAlt />
								</Icon>
							</CardContent>
						) : (
							<CardContent answer={this.state.answer}>
								<CardPart nope onClick={this.nope} />
								<CardPart like onClick={this.like} />
								<Title dotted={image !== 'url'}>{word}</Title>
								{image !== 'url' ? <Image src={image} alt={word} answer={translation}>
									<Answer content={translation} show={this.state.showAnswer} />
								</Image> : <Title hide={!this.state.showAnswer}>{translation}</Title>}
							</CardContent>
						)}
					</Card>
					{this.state.baseWord && <Decision>
						<Icon onClick={this.nope}>
							<FaTimes />
						</Icon>
						<Language src={this.state.languageOrder.word === 'word' ? pl : uk} title={`Zmie na jzyk ${this.state.languageOrder.word === 'word' ? 'polski' : 'angielski'}`} onClick={this.changeLanguage} />
						<Icon onClick={this.like}>
							<FaHeart />
						</Icon>
					</Decision>}
			</EasyMatchSite>
		);
	}
}

export default TinderApp