// BASIC
import React, { Component } from 'react'
import styled, {css, keyframes} from 'styled-components'
import {Link} from 'react-router-dom'

import {db, au, fu} from '../../../Config/firebase'
// IMAGES
import first from '../../Images/icons/podium/first.svg'
import second from '../../Images/icons/podium/second.svg'
import third from '../../Images/icons/podium/third.svg'
import diamond from '../../Images/icons/diamond.svg'
// ICONS
import {FaTimes} from 'react-icons/fa'

const StatsElement = styled.div`
	width: 100vw;
	height: 100vh;
	height: calc(var(--vh, 1vh) * 100);
	display: flex;
	flex-direction: column;
	align-items: center;
`
const Header = styled.header`
	margin: 30px;
	font-size: 40px;
	line-height: 40px;
	font-weight: bold;
`
const Main = styled.div`
	width: calc(100vw - 60px);
	height: calc(100vh - 150px);
	display: flex;
	justify-content: center;
	position: relative;
	overflow-y: scroll;
	::-webkit-scrollbar {
		width: 3px;
	}
	::-webkit-scrollbar-track {
		background: var(--color-dark); 
		border-radius: 3px;
	}
	::-webkit-scrollbar-thumb {
		background: var(--color-secondary);
	}
`
const UserList = styled.div`
	width: 100%;
	max-width: 500px;
	transition: transform 0.3s ease;
	${props =>
		props.active &&
		css`
			transform: translateX(-275px);
		`
	}
	@media (max-width: 1100px) {
		transform: translateX(0);
	}
`
const fade = keyframes`
	from {
		opacity: 0;
		transform: scale(0.5)
	}
	50% {
		opacity: 0.25;
		transform: scale(1.1)
	}
	to {
		opacity: 0.5;
		transform: scale(1)
	}
`
const Delete = styled(FaTimes)`
	position: absolute;
	bottom: 10px;
	right: 10px;
	opacity: 0.5;
	@media (hover: hover) {
		display: none;
		transition: all 0.1s ease;
		animation: ${fade} 0.3s ease;
		:hover {
			cursor: pointer;
			opacity: 1;
			transform: scale(1.1)
		}
	}
`
const UserWrapper = styled.div`
	position: relative;
	margin: 10px;
	border: 1px solid var(--color-secondary);
	border-radius: 10px;
	transition: all 0.1s ease;
	:nth-child(1) {
		::after {
			content: '';
			position: absolute;
			top: 10px;
			right: 10px;
			width: 50px;
			height: 50px;
			background: url(${first}) no-repeat;
			background-size: 50px;
		}
	}
	:nth-child(2) {
		::after {
			content: '';
			position: absolute;
			top: 10px;
			right: 10px;
			width: 50px;
			height: 50px;
			background: url(${second}) no-repeat;
			background-size: 50px;
		}
	}
	:nth-child(3) {
		::after {
			content: '';
			position: absolute;
			top: 10px;
			right: 10px;
			width: 50px;
			height: 50px;
			background: url(${third}) no-repeat;
			background-size: 50px;
		}
	}
	:hover {
		border: 1px solid var(--color-primary);
	}
	:hover ${Delete} {
		display: block;
	}
	${props =>
		props.premium &&
		css`
			:hover {
				border: 1px solid var(--color-decorative);
			}
		`
	};
`
const User = styled(Link)`
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 20px;
	text-align: center;
	font-weight: bold;
	text-decoration: none;
	color: var(--color-primary);
`
const Nick = styled.h1`
	margin-top: 0px;
	font-size: 25px;
	max-width: calc(100% - 100px);
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
`
const Points = styled.strong`
	font-size: 20px;
	color: var(--color-main);
`
const Diamond = styled.img`
	position: absolute;
	top: 10px;
	left: 10px;
	color: var(--color-main);
	width: 40px;
	:hover {
		cursor: pointer;
	}
`

class Stats extends Component {
	state = {
		isAdmin: false,
		users: null,
		books: null,
		hideBooks: false,
		active: false
	}
	componentDidMount() {
		au.onAuthStateChanged(user => {
			if (user) {
				user.getIdTokenResult().then(idTokenResult => {
					if (idTokenResult.claims.admin) {
						this.setState({isAdmin: true})
					}
				})
			}
		})
		db.collection('users').orderBy('easy-word.experience', 'desc').onSnapshot(snap => {
			if (this.state.users) {
				this.setState({users: null})
			}
			snap.docs.forEach(doc => {
				let users = this.state.users ? this.state.users : [];
				this.setState({
					users: users.concat(doc.data()),
				});
			})
		});
		db.collection('books').orderBy('info.name').get().then(snap => {
			snap.docs.forEach(doc => {
				let books = this.state.books ? this.state.books : [];
				this.setState({
					books: books.concat(doc.data()),
				})
			})
		});
	}
	changeActive = () => {
		!this.state.active ? this.setState({active: true}) : this.setState({active: false})
	}
	hideBooks = (e) => {
		e.persist();
		console.log(e.target.id);
		this.setState({hideBooks: true});
		setTimeout(() => {
			document.getElementById(e.target.id).parentElement.style.display = 'none';
		}, 500);
	}
	deleteUser = (uid) => {
		let decision = window.confirm(`Usun ${uid}?`)
		if (decision) {
			console.log(uid);
			const deleteUser = fu.httpsCallable('deleteUser');
			deleteUser(uid).then(result => {
				console.log(result);
			})
			db.collection('users').doc(uid).delete()
		}
	}
	render() {
		let users = this.state.users;
		return (
			<StatsElement>
				<Header>Ranking</Header>
					<Main>
						{!users ? 'Wczytywanie...' : <UserList active={this.state.active}>
							{users.map((user, index) => {
								const {nick, uid} = user.info;
								return (
									<UserWrapper>
										{this.state.isAdmin && <Delete onClick={() => this.deleteUser(uid)} />}
										<User premium={user['easy-word'].premium ? 1 : 0} to={`/users/${uid}`} id={uid} key={uid}>
											<Nick>{nick}</Nick>
											<Points>{user['easy-word'].experience}</Points>
											{user['easy-word'].premium ? <Diamond src={diamond} alt={uid} title="Uytkownik premium" /> : ''}
										</User>
									</UserWrapper>
								)
							})}
						</UserList>}
					</Main>
			</StatsElement>
		)
	}
}

export default Stats