const W = styled.span`
		background-color: black;
		color: gold;
		padding: 20px 0px;
		font-size: 30px;
		font-weight: bold;
		font-family: 'Ubuntu', sans-serif;
		letter-spacing: 2px;
		margin: 10px 0;
	`
const Ord = styled.span`
		background-color: black;
		color: lightgray;
		padding: 20px 20px 20px 0px;
		border-radius: 0px 20px 20px 0px;
		font-size: 30px;
		font-weight: bold;
		font-family: 'Ubuntu', sans-serif;
		letter-spacing: 2px;
		margin: 20px 20vw 20px 0px;
	`
class IndexPage extends Component {
	state = {
		recent: [],
		scrolled: false,
		stats: {
			views: "-",
			average: "-",
			users: "-"
		}
	}
	componentDidMount() {

		db.collection('apps').doc('easy-word').get().then(snap => {
			db.collection('apps').doc('easy-word').update({
				'stats.views': snap.data().stats.views + 1
			})
		})
		db.collection('apps').doc('easy-word').onSnapshot(snap => {
			let allStars = 0;
			let allOpinions = 0;
			snap.data().stats.opinions.forEach(({stars}) => {
				allStars += stars
				allOpinions += 1
			})
			const average = allStars / allOpinions
			this.setState(prevState => ({
				stats: {
					...prevState.stats,
					average: average.toFixed(1),
					views: snap.data().stats.views.toString()
				}
			}));
		})
		db.collection('users').onSnapshot(snaps => {
			let users = 0;
			snaps.forEach(() => users += 1)
			this.setState(prevState => ({
				stats: {
					...prevState.stats,
					users: users.toString()
				}
			}));
		})
	}
	scrollDown = (e) => {
		const nextelement = e.target.parentElement.nextSibling.children[0];
		nextelement.scrollIntoView({behavior: 'smooth', block: 'start'})
	}
	check = (e) => {
		const goal = e.target.children[1].children[0].getBoundingClientRect().top
		if (goal <= 0) {
			this.setState({scrolled: true})
		}
		else if (e.target.scrollTop > 0) {
			this.setState({scrolled: '...'})
		}
		else if (goal > 0) {
			this.setState({scrolled: false})
		}
	}
	render() {
		return (
			<>
				<Preloader/>
				<MainPageSite onScroll={this.check}>
					<Welcome logo={logo} stats={this.state.stats} scroll={this.scrollDown} />
					<Choise>
					<div>
					<E>E</E>
					<Asy>asy</Asy>
					<W>W</W>
					<Ord>ord</Ord>
				</div>
						<SectionTitle className="title">
							<Title>Liceum</Title>
							<Line />
						</SectionTitle>
						<BooksWrapper>
							<Book to='/repetytorium/spis-tresci'>
								<BookImage src="https://www.macmillan.pl/components/com_ssshop/cache/500x500/9788376218496.png" alt="Firma: Macmillan rdo: https://www.macmillan.pl/katalog/repetytorium-dla-uczniow-liceow-i-technikow-poziom-podstawowy-i-rozszerzony-wersja-wieloletnia,p-642-84"/>
							</Book>
							<Book to='/jezyk-angielski-zawodowy/spis-tresci'>
								<BookImage src="https://sklep.wsip.pl/typo3temp/pics/upload_temp_kyFIwn_1a19bdf063.jpg" alt="WSiP - jzyk angielski zawodowy w logistyce i spedycji. Zeszyt wicze"/>
							</Book>
						</BooksWrapper>
						<SectionTitle className="title">
							<Title>Propozycje</Title>
							<Line />
						</SectionTitle>
						<BooksWrapper>
							<Book to='/jezyki-programowania/spis-tresci'>
								<BookImage src={programmingBook} alt="test"/>
							</Book>
							<Book to='/znaki-drogowe/spis-tresci'>
								<BookImage src={roadSignsBookImage} alt="Znaki drogowe"/>
							</Book>
							<Book to='/czasowniki-nieregularne/spis-tresci'>
								<BookImage src={irregularVerbsBookImage} alt="Czasowniki nieregularne"/>
							</Book>
						</BooksWrapper>
						{!this.state.scrolled ? <Go to='/opinie'>
							<Stats>
								<FaStar />
							</Stats>
						</Go> : this.state.scrolled !== '...' ? <Go to='/ranking'>
							<Stats>
								<Cup />
							</Stats>
						</Go> : ''}
					</Choise>
				</MainPageSite>
			</>
		)
	}
}

export default IndexPage
